body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*:focus {
  outline: none !important;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.gallery-block.grid-gallery {
  padding-bottom: 60px;
  padding-top: 60px;
}

.gallery-block.grid-gallery .heading {
  margin-bottom: 50px;
  text-align: center;
}

.gallery-block.grid-gallery .heading h2 {
  font-weight: bold;
  font-size: 1.4rem;
  text-transform: uppercase;
}

.gallery-block.grid-gallery a:hover {
  opacity: 0.8;
}

.gallery-block.grid-gallery .item img {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.gallery-block.grid-gallery .item {
  margin-bottom: 20px;
}

.project-item {
  widows: 300px;
  height: 266px;
  display: block;
  position: relative;
  border-radius: 5px;
  background: #fffaf6;
  box-shadow: 2px 3px 5px 0px rgba(143, 140, 143, 1);
}

.project-item h1,
.project-item h6 {
  opacity: 1;
  -webkit-transition: opacity 2s; /* For Safari 3.1 to 6.0 */
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.project-item:hover h1,
.project-item:hover h6 {
  opacity: 0;
}

.project-item:hover .list-of-actions {
  opacity: 0.9;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.list-of-actions {
  /* height: 100%;
    width: 100%; */
  opacity: 0;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  padding: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

@media (min-width: 576px) {
  .gallery-block.grid-gallery .scale-on-hover:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.15) !important;
  }
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.myScrollableDiv {
  display: -webkit-flex;
  display: flex;
  overflow-x: auto;
  width: 100%;
}
.myCard {
  margin-right: 15px;
}

.myScrollableDiv::-webkit-scrollbar {
  display: none;
}

.card-vertical-scroll-enabled {
  height: 70vh;
  overflow-y: scroll;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.button__add-ticket {
  background: #fffaf6;
  padding: 5px;
  border-radius: 5px;
  -webkit-transition: box-shadow 0.2s;
  transition: box-shadow 0.2s;
  box-shadow: 0;
}

.button__add-ticket:hover {
  box-shadow: 0px 0px 3px 0.2px rgba(107, 107, 107, 1);
}
.ticket-text {
  font-size: 0.8em;
}

.priority-low {
  border-left: 4px solid #5fc745;
}

.priority-medium {
  border-left: 4px solid #ffc75e;
}

.priority-high {
  border-left: 4px solid #f75a47;
}
.card-delete {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.close-button {
  color: #696969;
  cursor: pointer;
}
.card--content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.swimLane-title {
  padding: 5px;
  width: 320px;
}

.swimLaneOptions {
  float: right;
  cursor: pointer;
}

/* Other styles for the page not related to the animated dropdown */

body {
  background: -webkit-linear-gradient(left, #0062e6, #33aeff);
  background: linear-gradient(to right, #0062e6, #33aeff);
}
.landingButton {
  text-decoration: none;
  margin: 0 10px;
}
.landingButton:hover {
  text-decoration: none;
}

